var test = require('ava')


function swap (arr) {


    for ( var i = 0, pivLoc = 0, piv = arr.length - 1 ; piv >= 1  ;) {

        if ( arr[i] <= arr[piv]){
            pivLoc = i++ 
        }
        else {
            var cur = arr[i]
            // var purp = arr[piv]
            arr[i] = arr[piv - 1] 
            arr[piv - 1] = arr[piv]
            arr[piv] = cur
            piv -= 1
        }
    }
    return arr
}

test('swaps pivot to correct point', t => {

    var arr = [ 1, 8, 2, 9, 7, 4 ]
    var act = swap(arr)
    var exp = [ 1, 2, 4, 9, 7, 8]

    t.deepEqual(act, exp)

    var arr2 = [  8, 2, 9, 7, 4]
    var act2 = swap(arr2)
    var exp2 = [  2, 4, 9, 7, 8]

    t.deepEqual(act2, exp2)

    var arr2 = [  9, 7, 4]
    var act2 = swap(arr2)
    var exp2 = [  4, 7, 9]

    t.deepEqual(act2, exp2)
})
